.table_Hw2 {
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    font-size: 14px;
    color: #bbffbb;
    background: #282c34;
    max-width: 70%;
    width: 30%;
    border-collapse: collapse;
    text-align: left;
}

th {
    font-weight: normal;
    color: #039;
    /*border-bottom: 2px solid #6678b1;*/
    padding: 10px 8px;
}

td {
    border-bottom: 1px solid #ccc;
    color: #669;
    padding: 9px 8px;
    transition: .3s linear;
    text-align: center;
}

tr:hover td {
    color: #6699ff;
}

.button7 {
    font-weight: 500;
    color: #141212;
    text-decoration: none;
    /*padding: .8em 1em calc(.8em + 3px);*/
    border-radius: 10px;
    background: yellow;
    box-shadow: 0 -3px rgb(53, 167, 110) inset;
    transition: 0.2s;
    margin: 2px;
    padding: 10px;
    width: 80px;
}

.button7:hover {
    background: rgb(53, 167, 110);
}

.button7:active {
    background: rgb(33, 147, 90);
    box-shadow: 0 3px rgb(33, 147, 90) inset;
}
